import { numToPercent } from '../utils/helper';

type CandleType = { preClose: number | string; open: number | string; hight: number | string; low: number | string; close: number | string };

export default function Candle(props: CandleType) {
    const { preClose, open, hight, low, close } = Object.entries(props).reduce((a, b) => {
        a[b[0]] = Number(b[1]);
        return a;
    }, {} as { [key: string]: number });

    const openRate = numToPercent((open - preClose) / preClose, 2);
    const closeRate = numToPercent((close - preClose) / preClose, 2);
    const hightRate = numToPercent((hight - preClose) / preClose, 2);
    const lowRate = numToPercent((low - preClose) / preClose, 2);

    const isWhite = openRate > closeRate;
    const isStationary = openRate === closeRate;

    return (
        <>
            <svg width='100%' height='100%' viewBox='0 0 60 80'>                
                {isStationary && (
                    <line
                        x1='10'
                        y1={40 - (isWhite ? openRate : closeRate)}
                        x2='50'
                        y2={40 - (isWhite ? openRate : closeRate)}
                        stroke='black'
                        strokeWidth='1'
                        strokeDasharray='1'
                    ></line>
                )}
                {!isStationary && (
                    <rect
                        x='10'
                        y={40 - (isWhite ? openRate : closeRate)}
                        width='40'
                        height={Math.abs(openRate - closeRate)}
                        fill={isWhite ? 'blue' : 'red'}
                    ></rect>
                )}
                <line x1='0' y1='40' x2='60' y2='40' stroke='black' strokeWidth='1' strokeDasharray='2'></line>
                <line x1='30' y1={40 - Math.abs(hightRate)} x2='30' y2={40 + Math.abs(lowRate)} stroke={isWhite ? 'blue' : 'red'} strokeWidth='1'></line>
            </svg>
        </>
    );
}
