import React, { ReactNode, useState } from 'react';
import styled from 'styled-components';

const NaviOutter = styled.div`
    display: flex;
    & > div {
        cursor: pointer;
        flex: 1;

        &.focus {
            border: 1px solid red;
        }
        &.unfocus {
            border: 1px solid transparent;
        }
    }
`;

export default function Navigator({ value: initValue = 0, navis, children }: { value?: number; navis: ReactNode[]; children?: ReactNode }) {
    const [value, setValue] = useState(initValue);
    const naviHandler = (val: number) => setValue(val);

    return (
        <div>
            <NaviOutter>
                {navis.map((el, i) => (
                    <div key={`navi-${i}`} className={value === i ? 'focus' : 'unfocus'} onClick={() => naviHandler(i)}>
                        {el}
                    </div>
                ))}
            </NaviOutter>
            <div>{React.Children.toArray(children)[value]}</div>
        </div>
    );
}
